
.gists-display-area {

}

@media screen and (min-width: 1100px) {   
    .gists-left-pane,.gists-right-pane {
        display: inline-block;
        vertical-align: top;
        width: 50%;
    }
}

.gists header {
    font-size: 42px;
    font-weight: bolder;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    text-align: center;
    border-style: solid;
    border-color: seagreen;
    border-width: 0 0 12px 0;
    padding: 8px;
}

.gists header span {
    margin: 0 12px;
}

.gists .left {
    text-align: left;
}

.hidden {
    display:none;
}

.small-font {
    font-size: 12px;
}


