.gist {
    display: list-item;
    list-style-position: outside;
    list-style-type: decimal;
    margin-left: 30px;
}

li.gist:nth-child(even) {
    background-color: whitesmoke;
}

.gist-title:hover {
    background-color: greenyellow !important;
}

.gist.opened {
    line-height: 14px;
}

.gist.opened .gist-title {
    font-weight: bolder;
    font-size: 14px;
    margin-left: -30px;
}

.gist.opened::before {
    content:"âž³";
    position: relative;
    left: -70px;
    top: 5px;
    color: red;
    font-weight: bolder;
    font-size: 36px;
}

.gist-title {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 12px;
    cursor: pointer;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    overflow: hidden;
}

.gist-icon {
    margin: 8px;
}

.gist-icon img {
    height: 64px;
    width: auto;
}



